import Vue from 'vue'
import Router from 'vue-router'

//import index from '@/components/index'


/*import JiShi from '../pages/jishi.vue' //启动页默认集市大组件	*/
import JiShi from '../pages/jishi/jishi.vue' //集市容器组件
	import HuoYueShiChang from '../pages/jishi/HuoYueShiChang.vue'	
	import HuoYueShiChangDouYou from '../pages/jishi/HuoYueShiChangDouYou.vue'	
	import HuoYueShiChangDouPo from '../pages/jishi/HuoYueShiChangDouPo.vue'	
	import QiYeZhiGong from '../pages/jishi/QiYeZhiGong.vue'
	import SanDaZhuLiang from '../pages/jishi/SanDaZhuLiang.vue'
	import SanDaZhuLiangCont from '../pages/jishi/SanDaZhuLiang_Cont.vue'
	import SanDaZhuLiangXQ from '../pages/jishi/SanDaZhuLiang_XQ.vue'
	import TuJieShiChang from '../pages/jishi/TuJieShiChang.vue'
	import GeRenDingYue from '../pages/jishi/GeRenDingYue.vue'
	import YingYangSiLiao from '../pages/jishi/YingYangSiLiao.vue'
	import YouZhiYouLiao from '../pages/jishi/YouZhiYouLiao.vue'
	import YouZhiYouLiaoCont from '../pages/jishi/YouZhiYouLiao_Cont.vue'
	import ZongHeChaXun from '../pages/jishi/ZongHeChaXun.vue'

import ZiXun from '../pages/zixun/ZiXun.vue' //资讯大组件
	/*资讯页内的路由*/
	import ConaList0 from '../pages/zixun/XinWen.vue'
	import ConaList1 from '../pages/zixun/DuJia.vue'
	import ConaList2 from '../pages/zixun/GuanDian.vue'
	import ConaList3 from '../pages/zixun/ZhengCe.vue'
	import ConaList4 from '../pages/zixun/GuanZhu.vue'
	import Content from '../pages/zixun/Content.vue'

import WoDe from '../pages/wode/WoDe.vue' //我的大组件
	import WoDeGuanZhu from '../pages/wode/WoDeGuanZhu.vue'
	import WoDeDingYue from '../pages/wode/WoDeDingYue.vue'
	import WoDeKeFu from '../pages/wode/WoDeKeFu.vue'
	import Setting from '../pages/wode/setting.vue'
	import WeDeShouCang from '../pages/wode/WeDeShouCang.vue'

import LoginPage from '../pages/utils/Login.vue'

/*import DataSharing from '../pages/data-sharing.vue' //数据共享大组件
import Report from '../pages/report.vue' //报告大组件*/

Vue.use(Router)
const routes = [
			{
				// path:'/', component:ZiXun,
				path:'/', component:LoginPage,
				children:[
					{path: '/', component: ConaList0 }, 
				]
			},
			{path:'/JiShi', component:JiShi},
				{path: '/JiShi/HuoYueShiChang', component:HuoYueShiChang},
					{path: '/JiShi/HuoYueShiChangDouYou', component:HuoYueShiChangDouYou},
					{path: '/JiShi/HuoYueShiChangDouPo', component:HuoYueShiChangDouPo},
				{path: '/JiShi/QiYeZhiGong', component:QiYeZhiGong},
				{path: '/JiShi/SanDaZhuLiang', component:SanDaZhuLiang},
					{name:'SanDaZhuLiang_Cont',path: '/JiShi/SanDaZhuLiang_Cont:q', component:SanDaZhuLiangCont},
					{path: '/JiShi/SanDaZhuLiangXQ', component:SanDaZhuLiangXQ},
					{name:'SanDaZhuLiangXQ',path: '/JiShi/SanDaZhuLiang_XQ:n', component:SanDaZhuLiangXQ},

				{path: '/JiShi/TuJieShiChang', component:TuJieShiChang},
				{path: '/JiShi/GeRenDingYue', component:GeRenDingYue},
				{path: '/JiShi/YingYangSiLiao', component:YingYangSiLiao},
				{path: '/JiShi/YouZhiYouLiao', component:YouZhiYouLiao},
					{name:'YouZhiYouLiaoCont' , path:'/JiShi/YouZhiYouLiaoCont:q', component:YouZhiYouLiaoCont},
				{path: '/JiShi/ZongHeChaXun', component:ZongHeChaXun},

			{
				name:'Zixun',path:'/ZiXun', component:ZiXun,
				children:[
					{path: '/', component: ConaList0 },
				    {path: '/ZiXun/ConaList0', component:ConaList0},
				    {path: '/ZiXun/ConaList1', component:ConaList1},
				    {path: '/ZiXun/ConaList2', component:ConaList2},
				    {path: '/ZiXun/ConaList3', component:ConaList3},
				    {path: '/ZiXun/ConaList4', component:ConaList4},
				],
			},
			{name:'ContentPage',path: '/ZiXun/Content', component:Content},
			/*{path:'/DataSharing', component:DataSharing},
			{path:'/Report', component:Report},*/
			{path:'/WoDe', component:WoDe},
				{path:'/WoDe/WoDeGuanZhu', component:WoDeGuanZhu},
				{path:'/WoDe/WoDeDingYue', component:WoDeDingYue},
				{path:'/WoDe/WoDeKeFu', component:WoDeKeFu},
				{path:'/WoDe/Setting', component:Setting},
				{path:'/WoDe/WeDeShouCang', component:WeDeShouCang},
			{name:'Login',path:'/Login', component:LoginPage},


	  ]

const router =  new Router({
 	  mode: 'history',
	  routes
})

export default router;
 



