<template>
  <div id="Layout" class="aui-flexView"> 

  	 <section class="aui-flexView" v-if="footers">
	     <!-- <header class="header_title title-container" v-if="changeHedr">
	        <div class="aux_back" v-if="auxBack"></div>
	        <h1 class="h1_title">三大主粮</h1>
	     </header> -->
	    <!--  <header class="head-container title-container header_title" v-if="!my">
	     	<div class="aux_back"  v-if="auxBack" @click="backPrv()"></div>
	        <div class="weather" v-if="information">
	            <span class="weather-city">北京</span>
	            <img class="weather-ico" src="../assets/img/sun-ico.png"/>
	            <span class="weather-num">31℃~20℃</span>
	        </div>
	        <p class="message-title text-cent">{{titleText}}{{this.$route.params.issubpage}}</p>    
    	</header> -->
    	<div style="height: 100%; overflow-y: auto;" >
    		<!-- <router-view></router-view> -->  <!-- v-on:childByValue="childByValue" -->

    		<!--  <keep-alive> -->
		        <router-view></router-view>
		     <!--  </keep-alive> -->
    	</div>

        <footer class="aui-footer">
        	<router-link to="/ZiXun" class="aui-tabBar-item">
		            <a href="javascript:;" class="aui-tabBar-item "
		            	@click="tabToggle(0)"
		            	:class="{'aui-tabBar-item-active':tabIndex === 0 }"
		            >
		                <span class="aui-tabBar-item-icon">
		                    <i class="icon icon-home"></i>
		                </span>
		                <span class="aui-tabBar-item-text">资讯
		                </span>
		            </a>
            </router-link>
        	 <router-link to="/JiShi" class="aui-tabBar-item">
		            <a href="javascript:;" class="aui-tabBar-item "
		            	@click="tabToggle(1)"
		            	:class="{'aui-tabBar-item-active':tabIndex === 1 }"
		            >
		                <span class="aui-tabBar-item-icon">
		                    <i class="icon icon-news"></i>
		                </span>
		                <span class="aui-tabBar-item-text">集市
		                </span>
		            </a>
             </router-link>
             
            <!-- <router-link to="/DataSharing" class="aui-tabBar-item">
	            <a href="javascript:;" class="aui-tabBar-item "
	            	@click="tabToggle(2)"
	            	:class="{'aui-tabBar-item-active':tabIndex ===2 }"
	            >
	                <span class="aui-tabBar-item-icon">
	                    <i class="icon icon-group"></i>
	                </span>
	                <span class="aui-tabBar-item-text">数据共享
	                </span>
	            </a>
            </router-link> -->
        	<!-- <router-link to="/Report" class="aui-tabBar-item">
		            <a href="javascript:;" class="aui-tabBar-item "
		            	@click="tabToggle(3)"
		            	:class="{'aui-tabBar-item-active':tabIndex ===3 }"
		            >
		                <span class="aui-tabBar-item-icon">
		                    <i class="icon icon-comm"></i>
		                </span>
		                <span class="aui-tabBar-item-text">报告
		                </span>
		            </a>
            </router-link>  -->
            <router-link to="/WoDe" class="aui-tabBar-item">
		            <a href="javascript:;" class="aui-tabBar-item"
		            	@click="tabToggle(2)"
		            	:class="{'aui-tabBar-item-active':tabIndex === 2 }"

		            >
		                <span class="aui-tabBar-item-icon">
		                    <i class="icon icon-me"></i>
		                </span>
		                <span class="aui-tabBar-item-text">我的
		                </span>
		            </a>
            </router-link> 
        </footer>
    </section>
  <!--  <router-view></router-view> -->
  </div>
</template>

<script>
	//import DataSharing from './pages/data-sharing.vue'
	//import Report from './pages/report.vue'
	export default {
		  name: 'Layout',
		  data(){
		  	return {
		  		tabIndex:0, //底部导航 高亮索引
		  		information:true, //用于显示天气消息
		  		auxBack:false, //用于隐藏返回箭头
		  		titleText:'资讯',
		  		currenLoc:0, //记录底部当行当前位置
		  		footers:true
		  	}
		  },
		  props:{
			//subpage
		  },
		  methods:{
			tabToggle(index){
				this.tabIndex = index;
				sessionStorage.currenLoc = index;
			},
			 open() {
		        this.$alert('这是一段内容', '标题名称', {
		          confirmButtonText: '确定',
		          callback: action => {
		            this.$message({
		              type: 'info',
		              message: `action: ${ action }`
		            });
		          }
		        });
		      },

	       open2() {
		        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '删除成功!'
		          });
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消删除'
		          });          
		        });
		      }


			
		  },
		  components:{
		  	
		  },
		  mounted(){
		  	if(sessionStorage.currenLoc){
		  		this.tabIndex = parseInt(sessionStorage.currenLoc)
		  	}else{
		  		this.tabIndex = 0
		  	}

		  	//this.$layer.alert('是不是')
			//this.$layer.msg('添加成功');
		  	//this.$layer.confirm('添加成功');

		  	// this.open2();
		 }

	}
</script>

