<!-- 导航下的我的关注页面 -->
<template>
	<div id="WoDeGuanZhu">
		<v-headr :myname="myname"></v-headr>
		<div class="my-concern-container">
			<!-- 先写死得了 -->
	        <!-- <ul class="my-concern-warp aui-card-list" v-for="item in datas">
	            <li class="my-concern-list aui-panel-cell">
	                <div class="my-concern-pic "></div>
	                <div class="aui-panel-cell-bd">
	                    <h4>{{item.src}}</h4>
	                </div>
	                <div class="right_tex my-concerned">√ 已关注</div>
	            </li>
	        </ul> -->

	        <ul class="my-concern-warp aui-card-list">
	            <li  v-for="item in datas"
	            class="my-concern-list aui-panel-cell">
	                <div class="my-concern-pic "></div>
	                <div class="aui-panel-cell-bd">
	                    <h4>{{item.Title}}</h4>
	                </div>
	                <div class="right_tex my-concerned">√ 已关注</div>
	            </li> 
	        </ul>
	    </div>




	    <div class="add-more-con">
	        <a class="add-more" href="javascript:;">
	            <span class="add-pic"></span>
	            <span class="add-btu">添加更多</span> 
	        </a>
	    </div>
	</div>
</template>

<script type="text/javascript">
	const dataSrc = JSON.parse(sessionStorage.datas) //数据源 
	import Headr from '../../components/header.vue' 
	export default {
		name:'WoDeGuanZhu',
		data(){
			return {
				msg:'这是我的导航下的我的关注页面',
				myname:{
					name:'我的关注',
					IsSubPage:true,
				},
				datas:null 
			}
		},
		components:{
			'v-headr':Headr,
		},
		methods:{
			childClick(){
				this.$emit('childByValue',this.IsSubPage)
			},
			init_gz(){
				var scThis = this; 
				var user_id = sessionStorage.userId; 
				this.axios.get('/api/flxoa//Follow?tokenid=000000&UserID=admin').then(function(response){ 
					console.log('--------------------收藏---------------------')
					console.dir(response.data[0].data);
	                scThis.dataFavorite = response.data[0].data;
	            });  
			}

		},
		mounted(){
			this.childClick()
			this.init_gz();
			//alert(22)
		}

	}
</script>


