<!-- 个人订阅 -->
    <template>
        <div id="GeRenDingYue">
            <v-headr :myname="myname"></v-headr>
            <div class="bod-list-container">
                <ul class="list-warp" v-for="(item,key) in DingYueShuJu">
                     <mt-cell-swipe
                          :right="[
                            {
                              content: '删除',
                              style: { background: 'red', color: '#fff' },
                              handler: function(){ removeSub(item.CropCategoryCode)}
                            }
                          ]">

                            <li class="list-con clearfix">
                                
                                <div class="list-num clearfix ">
                                    <span class="list-num-percentage float-lft">{{item.Region}}<i class="symbol-percentage">{{item.date}}</i></span>

                                    <p class="list-title-text">{{item.CropCategoryName}}</p>
                                    <span class="list-num-money float-rigt"><i class="symbol-money"></i> {{item.SubscribeCount}}订阅</span>
                                </div>
                           
                             </li>

                        </mt-cell-swipe>
                    
                    
                </ul>
                
                
            </div>
            
        </div>
    </template>

    <script type="text/javascript">
    import Headr from '../../components/header.vue' 
    const DingYueJS = JSON.parse(sessionStorage.dingyue);
    var user_id = sessionStorage.userId;
    if(user_id == null){
        user_id = '000000';
    }

    export default {
        name:'GeRenDingYue',
        data(){
            return {
                myname:{
                    name:'个人订阅',
                    IsSubPage:true
                },
                DingYueShuJu:null

                
                
            }
        },
        components:{
            'v-headr':Headr,
        },
        methods:{
            getDate(){
                this.getSubscribe();
                // this.DingYueShuJu = DingYueJS.result[0].JiShi
                // console.log(this.DingYueShuJu)
            },
           removeSub(key) {
                console.log(key)
                /*此处应该是吧对应的一条数据删了就ok*/ 
                this.axios.delete('/api/flxoa/Subscribe', {
                  tokenid: user_id,
                  SubscribeID:key, 
                  })
                  .then(function (response) {
                    console.log(response);
                    this.$toast({message: '删除成功',duration: 2000});
                    this.getDate()
                  })
                  .catch(function (error) {
                      console.log(error);
                  });  
             
            },
            getSubscribe:function(){ var sThis = this;
                
                this.axios.get('/api/flxoa/Subscribe?tokenid='+user_id+'&Type=0&UserID='+user_id).then(function(response){ 
                    console.log('--------------------获取---------------------')
                    console.dir(response);
                    sThis.DingYueShuJu = response.data[0].data
                });  
            }


        },
        mounted(){
            this.getDate()
            //this.$indicator.open('加载中...');
            //this.$messagebox('delete')


        }

    }
</script>
<style type="text/css">
    .mint-cell-title {
            -webkit-box-flex: 0;
            -ms-flex: 0;
            flex: 1;
            flex: 0;
    }
    .mint-cell-value {
        color: #888;
        /* display: -webkit-box; */
        display: -ms-flexbox;
        /* display: flex; */
        /* -webkit-box-align: center; */
        -ms-flex-align: center;
        align-items: center;
        width: 100%;
    }
    .mint-cell-value {
        color: #888;
        /* display: -webkit-box; */
        display: -ms-flexbox;
        /* display: flex; */
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        display: inline-block;
    }
    .list-warp li.list-con {
        border-bottom: 0; 
       
    }
    
    .mint-cell-swipe-button {
        
        line-height: 64px;
    }
    .list-title-text{ float:left;}
    .list-con { width:100%;}

    a.mint-cell{
        padding: 0 15px 0 0;
    }
    a.mint-cell-swipe-button{
        line-height: 52px
    }

</style>


 


