<!-- 集市下面的油脂油料组件 -->
<template>
	<div id="YouZhiYouLiao">
        <v-headr :myname="myname"></v-headr>
		 <section class="aui-scrollView YouZhiYouLiao">  
                <div class="aui-card-list"> 
                    <div v-show="sosuo">
                        
                    

                    <div class="area-title"><h4><span class="more">更多</span>热门品种</h4></div>
                    <ul class="yy-mod">
                        <li @click="goToSerch('0551')" class="main_cat active">大豆</li> 
                        <li @click="goToSerch('0557')" class="main_cat">棉籽</li>
                        <li @click="goToSerch('0444')" class="main_cat">二级芝麻油</li>
                        <li @click="goToSerch('0439')" class="main_cat">菜籽油</li>
                        <li @click="goToSerch('0445')" class="main_cat">精炼茶籽油</li>
                        <li @click="goToSerch('0435')" class="main_cat">进口豆油</li> 
                    </ul>

                    <div class="area-title"><h4><span class="more">更多</span>油厂报价</h4></div>
                    <ul class="yy-mod">
                        <li @click="goToSerch('0446')" class="main_cat active">24度棕榈油</li> 
                        <li @click="goToSerch('0439')" class="main_cat">一级菜籽油</li>
                        <li @click="goToSerch('0440')" class="main_cat">一级花生油</li>
                        <li @click="goToSerch('0436')" class="main_cat">四级豆油</li>
                        
                    </ul>

                    </div>


                   <div v-show="liebiao">
                       <a href="javascript:void(0);"
                       class="aui-panel-cell"
                       v-for="(item,index) in list" > 

                        <div class="aui-panel-cell-bd">
                            <h4>{{item.title}}</h4>
                            <span>{{item.date}}</span>
                        </div> 
                        <div class="float-rigt">
                            <em class="jiage">{{item.price}}元</em>
                        </div>
                    </a>
                   </div>
                     
                </div>
                

            </section>
	</div>
</template>

<script type="text/javascript">
    const youzhiyouliao =  sessionStorage.youzhi;
    const dataList_YoZhi = JSON.parse(youzhiyouliao);
    console.log("油脂")
    console.dir(dataList_YoZhi)
    import Headr from '../../components/header.vue'
	export default {
		name:'YouZhiYouLiao',
		data(){
			return {
				msg:'集市下面的油脂油料组件',
                myname:{
                    name:'油脂油料',
                    IsSubPage:true
                },
                list:null,
                sosuo:true,
                liebiao:false
			}
		},
        components:{
            'v-headr':Headr,
        },
		methods:{
            getUrlParam(key) {
      // 获取参数
                var url = window.location.search;
                // 正则筛选地址栏
                var reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)");
                // 匹配目标参数
                var result = url.substr(1).match(reg);
                //返回参数值
                return result ? decodeURIComponent(result[2]) : null;
            },
			childClick(){
				this.$emit('childByValue',this.IsSubPage)
			},
            init_list(){
                console.log("初始化列表"); 
                var isSear = this.getUrlParam("isSearch");
                var lisCity = this.getUrlParam("Cty");

                if(isSear!=null){
                    this.liebiao = true;
                    this.sosuo = false;

                    var tmpArr = new Array();
                    var list_1 = dataList_YoZhi.result[0].JinKouDaDou;
                     var list_2 = dataList_YoZhi.result[0].YouYongDaDou;
                     var list_3 = dataList_YoZhi.result[0].ShiYongDaDou;
                 for(let i in list_1){tmpArr.push(list_1[i])}
                    for(let j in list_2){tmpArr.push(list_2[j])}
                    for(let k in list_3){tmpArr.push(list_3[k])}   
                    this.getliebiao(tmpArr);

                }
            
            },
            goMore(){

            },
            goCity(key){
                this.$router.push({ name: 'YouZhiYouLiaoCont', query: { q: key }})
            },
            getliebiao(list_1){
                console.log(list_1)
                 this.list= list_1; 
            }
		},
		mounted(){



		  	this.childClick();
            this.init_list();

		  }
	}
</script>


