<!-- 集市下面的三大主粮组件 -->
<template>
	<div id="SanDaZhuLiangCont">
        <v-headr :myname="myname"></v-headr>
		<section class="aui-scrollView SanDaZhuLiangCont">  
                <div class="aui-card-list"> 

                    <a  href="javascript:void(0);"
                       class="aui-panel-cell"
                       v-for="key in datas"> 
                        <div class="aui-panel-cell-bd">
                            <h4>{{key.title}}</h4>
                            <span>{{key.date}}</span>
                        </div> 
                        <div class="float-rigt">
                            <em class="jiage">{{key.price}}元</em>
                        </div>
                    </a>

                </div>
            </section>
	</div>
</template>
<style scoped>
    #Headr{ height: 44px; }
</style>
<script type="text/javascript">

   const SanDa_ZhuLiang =  sessionStorage.SanDa;
   const dataList = JSON.parse(SanDa_ZhuLiang);
   console.dir(dataList)
    import Headr from '../../components/header.vue'
	export default {
		name:'SanDaZhuLiang_Cont',
		data(){
			return { 
                isA:0,
				msg:'集市下面的三大主粮组件',
                myname:{
                    name:'三大主粮',
                    IsSubPage:true
                },
                datas:null
				
			}
		},
        components:{
            'v-headr':Headr,
        },
		methods:{
             loadList:function(){
                 //
             },
             getLIst:function(){
                let qStr = this.$route.query.q;
                const query_arr = eval('dataList.result[0].'+qStr);
                console.log(query_arr)
                this.datas   = query_arr;
             },
			 goInfo:function(){

             },
             goMore:function(q){
                console.log(q);
             },
             tabToggle:function(index){
                 
             }
		},
		mounted(){  
            this.getLIst();
		}
	}
</script>


