<!-- 大的资讯组件 -->
<template>
	<div id="ZiXun">
		<!-- <header class="head-container title-container">
	        <div class="weather">
	            <span class="weather-city">北京</span>
	            <img class="weather-ico" src="../assets/img/sun-ico.png"/>
	            <span class="weather-num">31℃~20℃</span>
	        </div>
	        <p class="message-title text-cent">资讯</p>    
    	</header> -->
    	<v-headr :myname="myname"></v-headr>
	    <nav class="nav-container">
	        <ul class="nav-wrap " >
		            <li class="nav-item " 
			            v-for="(item,key) in navList"
			            @click="tabToggle(key)"
			            v-bind:class="{'active':tabIndex === key}"
			            v-if="item == '新闻' || item == '政策' || item == '关注'"
			            > 
				            <router-link :to="{path:'/ZiXun/ConaList'+key}">{{item}}</router-link> 
			            	<div class="bar" :class="{'active':tabIndex === key}"></div>
		            </li>
	        </ul>
	    </nav>
	    <!-- 新闻组件 -->
	  <!--   <v-news></v-news> -->
	   <!--  <keep-alive> -->
	   		 <router-view></router-view>
    	<!-- </keep-alive> -->
	</div>




</template>

<script type="text/javascript">
	//import News from './XinWen.vue';
	import Headr from '../../components/header.vue'

	export default {
		name:'ZiXun',
		data(){
			return {
				myname:{
					name:'资讯',
					
				},

				msg:'我是一个大的资讯组件',
				tabIndex: 0,
				navList:["新闻","独家","观点","政策","关注"],
			}
		},
		methods:{
			tabToggle(index){
				this.tabIndex = index
			}
		},
		components:{
			'v-headr':Headr,
			
			

		},
		mounted(){
			

		}
 

	}
</script>

<style type="text/css" >
	a{color: inherit}
</style>


