<!-- 集市下面的活跃市场组件 -->
<template>
	<div id="HuoYueShiChang">
		<v-headr :myname="myname"></v-headr>
		<div class="act-bazaar-title-warp">
        	<div class="act-bazaar-title text-cent">近一周涨幅情况</div>
    	</div>
	   
    	<div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10"  class="act-bazaar-container">
	        <ul class="bazaar-list-warp">
	            <li class="bazaar-list clearfix" v-for="(item, key) in list">
	                <div class="list-num float-lft ">
	                    <h3 class="price-title">{{item.HxCropofferStr}}</h3>
	                    <p class="price-reason">{{item.OfferDate}}</p>
	                </div>
	                <div class="price-num-up float-rigt">
	                	<span class="up">
	                		 <span class="up">+</span><em class="price-num">{{item.Price}}</em><span>%</span>
	                	</span>
	                   <!-- <span class="dow">
	                		 <span class="dow">-</span><em class="price-num">0.71</em><span>%</span>
	                	</span> --> 
	                </div>

	            </li>


	        </ul>
	         <img src="../../assets/img/Loading.gif" class="loading" v-if="loading">
	    </div>
	    <!-- <ul
		  v-infinite-scroll="loadMore"
		  infinite-scroll-disabled="loading"
		  infinite-scroll-distance="10">
		  <li v-for="item in list">{{ item }}</li>
		</ul> -->
	    
	</div>
</template>

<script type="text/javascript">
	import Headr from '../../components/header.vue' 
	export default {
		name:'HuoYueShiChang',
		data(){
			return {
				msg:'集市下面的活跃市场组件',
				myname:{
					name:'活跃市场',
					IsSubPage:true
				},
				sendatestar:"2017-6-27",
				PerPageNum:20,
				PageNum:0,
				loading:true,
				list:[],
				busy:false,//默认允许滚动执行loadMore函数




			}
		},
		methods: {
	      childClick () {
	        // childByValue是在父组件on监听的方法
	        // 第二个参数this.childValue是需要传的值
	        this.$emit('childByValue', this.IsSubPage)
	      },

	      /*获取当前时间*/
	      getNowFormatDate() {
		        var date = new Date();
		        var seperator1 = "-";
		        var year = date.getFullYear();
		        var month = date.getMonth() + 1;
		        var strDate = date.getDate();
		        if (month >= 1 && month <= 9) {
		            month = "0" + month;
		        }
		        if (strDate >= 0 && strDate <= 9) {
		            strDate = "0" + strDate;
		        }
		        var currentdate = year + seperator1 + month + seperator1 + strDate;
		        return currentdate;
		   },
	      /*请求数据*/
	      getData(flag){
	      	 /* this.axios.post('/api/flxoa/CropOffer',{
			        tokenid:'000000',
			        OfferDateStart:this.sendatestar,
			        PerPageNum:this.PerPageNum,
			        PageNum:this.PageNum,
		        
		      })*/

		      this.axios.get('/flxoa/CropOffer',{
			      	params:{
			      		tokenid:'000000',
				        OfferDateStart:this.sendatestar,
				        PerPageNum:this.PerPageNum,
				        PageNum:this.PageNum,
			      	}
			        
		        
		      })
		      .then(res => {
		      	console.log(res)
		      		/*if(res.status == 200){}*/
	      			if(flag){
	      				this.list =	this.list.concat(res.data[0].data)
	      				/*如果是最后一条则不再请求*/
	      				if (res.data[0].data.length<10) {
					            this.busy = true
					            this.loading =false
					          } else {
					            this.busy = false
				          }

	      			}else{
	      				this.list = res.data[0].data
	      				this.busy = false
	      			}
		      		//console.log(this.list)
		        //console.log('活跃成功'+JSON.stringify(res));
		      }, err => {
		        // 错误回调
		        console.log('活跃失败');
		      }) 
		      .catch(error => {
			    console.log(error);
			  }); 
			},



			loadMore() {
				//alert("++")
				  this.busy = true;
				  this.getData(true);
				  setTimeout(() => {
				  	/*console.log(this.PageNum)*/
				  	this.PageNum++;
				  	
				  	/*console.log(this.PageNum)*/
				    this.busy = false;
				  }, 2500);
			}





	    },
	    components:{
			'v-headr':Headr,
		},
		created(){
	      
	    },
		mounted(){
			//this.sendatestar = this.getNowFormatDate()
			this.getData()
		  	this.childClick();
		  	

				





		  }
	}
</script>
<style type="text/css" scoped>
	.up {
     color: #fe7f1e; 
	}
	.dow {
     color: #24b35b; 
	}
	.loading{
		display: block;
    	margin: 0 auto;
	}
</style>


