<template>
  <div id="Headr"> 

  
	     <!-- <header class="header_title title-container" v-if="changeHedr">
	        <div class="aux_back" v-if="auxBack"></div>
	        <h1 class="h1_title">三大主粮</h1>
	     </header> -->
	     <header class="head-container title-container header_title" >
	     	<div class="aux_back"  v-if="auxBack" @click="backPrv()"></div>
	       <!--  <div class="weather" v-if="information">
	            <span class="weather-city">北京</span>
	            <img class="weather-ico" src="../assets/img/sun-ico.png"/>
	            <span class="weather-num">19℃~5℃</span>
	        </div> -->
	        <p class="message-title text-cent">{{titleText}}<!-- {{this.$route.params.issubpage}} --></p>    
    	</header>
    	
  
  <!--  <router-view></router-view> -->
  </div>
</template>

<script>
	
	//import Information from './pages/information.vue' /*引入资讯大的组件*/
	//import Index from './pages/index.vue' /*引入集市大的组件*/
	//import My from './pages/my.vue'
	//import DataSharing from './pages/data-sharing.vue'
	//import Report from './pages/report.vue'
	export default {
		  name: 'Headr',
		  data(){
		  	return {
		  		titleText:'',
		  		auxBack:false,
		  		information:false

		  

		  	}
		  },
		  props:{
			myname:Object

		  },
		  methods:{
			/*判断接受来自哪个父组件去改变头部的消息*/
			whichFather(partCompt){
				if(partCompt.name){
					this.titleText = partCompt.name
				}
				if(partCompt.name == "资讯"){
					this.information = true
				}
				if(partCompt.IsSubPage == true){
					this.auxBack = true
				}
			},
			backPrv(){
		      	this.$router.go(-1)
		      }

		  },
		  components:{

		  },
		  mounted(){
		  	this.whichFather(this.myname)
		  }

	}
</script>

<style>

</style>



