<!-- 资讯页内的政策项组件 -->
<template>
	<div id="Policy">
 
    	





    	<div class="bod-container">
    		<v-headr :myname="myname"></v-headr>
	        <ul class="bod-warp">

	            <li class="bod-list clearfix" v-for="item in dataArr" >
	               
	                <div class="bod-list-info zixun">
	                    <h3 class="bod-list-title" @click="goDetail(item.NewsID)">{{item.Title}}</h3>
	                   <div>
	                   	
	                   	<em>分类:{{item.NewsCategoryName}}</em>
	                    	<em>时间:{{item.PublishDt}}</em>
	                    	<em>来源:{{item.SourceName}}</em>


	                   </div> 
	                    <br>

	                    <div class="clearfix">
	                    	<p>{{item.Content}}</p>
	                    </div>
	                </div>
	            </li>
 
	        </ul>
    	</div>

	</div>





	</div>
</template>

<script type="text/javascript">
	//import NewsList from './xinwen/news-list.vue';/*导入新闻列表组件*/
	//import Banner from './xinwen/banner.vue';/*导入轮播组件*/
	import Headr from '../../components/header.vue'
import Swiper from 'swiper' 
import 'swiper/dist/css/swiper.min.css';


	const dataSrc = JSON.parse(sessionStorage.dataszx) //数据源
	console.log(dataSrc)
	export default {
		name:'Policy',
		data(){
			return {
				myname:{
					name:'资讯',
					IsSubPage:true,
					information:false
				},

				dataArr:null,
			}
		},
		components:{
			'v-headr':Headr,
			
			

		},
		methods:{
			 getUrlParam(key) {
		      // 获取参数
		        var url = window.location.search;
		        // 正则筛选地址栏
		        var reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)");
		        // 匹配目标参数
		        var result = url.substr(1).match(reg);
		        //返回参数值
		        return result ? decodeURIComponent(result[2]) : null;
		    },
			lunbo:function(){
				 var mySwiper = new Swiper ('.swiper-container', {
			        loop: true, // 循环模式选
			        pagination: {
			          el: '.swiper-pagination',
			        },
			        autoplay: {
				       /* delay: 3000,*/
				        disableOnInteraction: false,
				     },
		        }); 
			},
			init_zhengce:function(queryid){
				var sThis = this;
				var user_id = sessionStorage.userId;
				if(user_id == null) user_id = '000000';
				 // http://hx.mac/api/flxoa/News?tokenid=000000&UserID=18810755213
				this.axios.get('/api/flxoa/News?tokenid='+user_id+'&Type=0&NewsID='+queryid).then(function(response){ 
					console.log('--------------------获取---------------------')
					console.dir(response);
	                sThis.dataArr = response.data[0].data
	            }); 
			},
			goFollow:function(zixunId){
				console.log(zixunId);
			},
			Subscribe:function(){
				
			}
		},
		mounted(){
			// var qid = this.$route.params.contId;
			var ser_id =  this. $route.query.qid; console.log(ser_id)
			this.lunbo(); 
			this.init_zhengce(ser_id);

			

		}
	}
</script>
<style scoped>
#Policy { background:#fff;}
	.bod-list .bod-list-info,.zixun{ 
		width: 100%;
		font-size:14px;
		padding-top:44px;
	}
	.bod-list-title{
		font-size:16px; margin-bottom:15px;white-space: inherit; width:100%;
	}
.concern{ width: 38px; }
	.guanzhu {     border: 1px solid #7b7d7f;color: #6a6a6a; }

	.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{
        bottom: 32px;
    }

    .swiper-pagination-bullet-active{
        background:#fff; 
    }
</style>


