　　特色优势原则。江苏是我国重要的优质水稻产区。清彻的水质、肥沃的土壤、丰富的降水、充足的光照以及水稻生长后期较大的昼夜温差，造就了苏米清香可口、软硬适中、糯而不黏的特点。为彰显优质“苏米”的特色，标准制订过程中，以具有一定生产规模的企业生产的优质大米为数据源，在调研、分析数据基础上，设定“苏米”标准相关技术参数。<br>
　　优质品牌原则。标准着眼于通过“苏米”标准的推广实施，进一步提高江苏大米产品质量，扩大“苏米”影响力，打造“苏米”品牌，促进江苏稻米产业的可持续健康发展。<br>
