<template>
	<div id="banner">
		<div class="banner-container">
	        <div class="swiper-container">
	            <div class="swiper-wrapper">

	                <!-- <div class="swiper-slide "
	                	 v-for="item in dataFlash">
	                    <img class="banner-img width100" src="../../../assets/img/ban01.png"/>
	                    <div class="shade-backg colr-w  lucency text-cent">{{item.title}}</div> 
	                </div>  -->


	                 <div class="swiper-slide ">
	                    <img class="banner-img width100" src="../../../assets/img/ban01.png"/>
	                    <div class="shade-backg colr-w  lucency text-cent">辽宁大连港进口大豆</div> 
	                </div> 
	                <div class="swiper-slide ">
	                    <img class="banner-img width100" src="../../../assets/img/ban02.png"/>
	                    <div class="shade-backg colr-w  lucency text-cent">天津天津港进口大豆</div> 
	                </div> 
	                <div class="swiper-slide ">
	                    <img class="banner-img width100" src="../../../assets/img/ban03.png"/>
	                    <div class="shade-backg colr-w  lucency text-cent">黑龙江佳木斯蛋白豆粕</div> 
	                </div> 



	            </div>
	             <div class="swiper-pagination"></div>
	        </div>
    	</div>

	</div>
		
</template>

<script type="text/javascript">
	import Swiper from 'swiper' 
	import 'swiper/dist/css/swiper.min.css';
	const dataBanner = JSON.parse(sessionStorage.dataBanner) //数据源 
	console.log(dataBanner);
	export default {
		name:'banner',
		data(){
			return{
				msg:'新创的轮播',
				dataFlash:null
			}
		},
		components:{
			/*'v-conlistnews':ConListNews*/
		},
		methods:{
			lunbo:function(){
				 var mySwiper = new Swiper ('.swiper-container', {
			        loop: true, // 循环模式选
			        pagination: {
			          el: '.swiper-pagination',
			        },
			        autoplay: {
				        delay: 3000,
				        disableOnInteraction: false,
				     },
		        }); 
			}
		},
		mounted(){
			this.dataFlash = dataBanner.result;
			this.lunbo(); 



		}
	}
</script>
<style type="text/css" >
	.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{
        bottom: 32px;
    }

    .swiper-pagination-bullet-active{
        background:#fff; 
    }
</style>


