<!-- 我的收藏 -->
<template>
	<div id="WdDeShouCang">
		<v-headr  :myname="myname"></v-headr>
    	<div class="collected">
	        <ul class="collected-warp">
	            <li class="bod-list clearfix"
	            	v-for="item in dataFavorite"
	            >
	                <!-- <div class="bod-list-img float-lft">
	                    <img class="list-img width100 " src="../../assets/img/list-img.png"/>
	                </div>                 -->
	                <div class="collected-info float-rigt">
	                	<!-- <span class="collect-type">新闻</span> -->
	                    <p class="collected-list-title">{{item.Title}}...</p>
	                    <!-- <ul class="source-wrap clearfix">
	                        <li class="float-lft collect-text">来源：<span>粮油信息网</span></li>
	                        <li class="float-rigt collect-start"></li>
	                    </ul> -->
	                    <!-- <ul class="time-wrap clearfix"> -->
	                        <!-- <li class="float-lft collected-time">2018-09-18</li> -->
	                        <!-- <li class="float-rigt text-rigt message"><img class="message-ico" src="../assets/img/message.png" />412</li> -->
	                        <!-- <li class="float-rigt text-rigt check collected-nub"><img class="check-ico" src="../../assets/img/check.png" />1108</li> -->
	                    <!-- </ul> -->
	                </div>
	            </li>

	           



	        </ul>
    	</div>
	</div>

</template>

<script type="text/javascript">
	import Headr from '../../components/header.vue' 
	export default {
		name:"WdDeShouCang",
		data(){
			return {
				myname:{
                    name:'我的收藏',
                    IsSubPage:true
                },
                dataFavorite:null
			}
		},
		components:{
            'v-headr':Headr,
        },
        methods:{ 
        	init_shoucang:function(){
        		var scThis = this; 
				var user_id = sessionStorage.userId; 
				if(user_id == null) user_id = '000000';
				this.axios.get('/api/flxoa/Favorite?tokenid='+user_id).then(function(response){ 
					console.log('--------------------收藏---------------------')
					console.dir(response.data[0].data);
	                scThis.dataFavorite = response.data[0].data;
	            });  
        	}
        },
        mounted(){
        	this.init_shoucang();
        }
	}
</script>
<style type="text/css" scoped>
	.collected{margin-top:44px}
.collect-type{display:inline-block;position:absolute;top:0;left:0;width:26px;height:16px;line-height:16px;text-align:center;border:1px solid #ef7776;font-size:11px;color:#ef7776}
.collected-info{position:relative;width:100%}
.collected-list-title{font-size:12px;color:#4a4a4a;font-weight:600;width:86%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;padding-left:35px}
.collect-start{width:13px;height:13px;margin-right:10px;background-image:url(../../assets/img/star.png);background-size:100%;background-repeat:no-repeat}
.collect-text{font-size:10px;color:#9b9b9b}
</style>


