<!-- 集市下面的营养饲料组件 -->
<template>
    <div id="YingYangSiLiao">
         <v-headr :myname="myname"></v-headr>
        <section class="aui-scrollView YingYangSiLiao">  
                <div class="aui-card-list"> 
                    <div class="area-title"><h4><span class="more">更多</span>热门品种</h4></div>
                    <ul class="yy-mod">
                        <li class="main_cat active" @click="goToSerch('1101')">玉米</li> 
                        <li class="main_cat" @click="goToSerch('0561')">花生粕</li>
                        <li class="main_cat" @click="goToSerch('0553')">豆粕</li>
                        <li class="main_cat" @click="goToSerch('0112')">DDGS</li> 
                        <li class="main_cat" @click="goToSerch('0114')">玉米胚芽粕</li>
                        <li class="main_cat" @click="goToSerch('0109')">玉米淀粉</li>
                    </ul>

                    <div class="area-title"><h4><span class="more">更多</span>最新价格</h4></div>

                   <a href="javascript:void(0);" class="aui-panel-cell" v-for="(item,key) in dataList">
                        <div class="aui-panel-cell-bd">
                            <h4>{{item.HxCropofferStr}}</h4>
                            <span>{{item.OfferDate}}</span>
                        </div> 
                        <div class="float-rigt">
                            <em class="jiage  " >{{item.Price}}{{item.up}}<span class="jiantou">↑</span></em> <!-- v-if="item.up == 1" -->
                           <!--  <em class="jiageD " v-if="item.up == 0">{{item.price}}{{item.up}}<span class="jiantou">↓</span></em> -->
                        </div>
                    </a>
                </div>
                

            </section>
    </div>
</template>

<script type="text/javascript">
    import Headr from '../../components/header.vue'

    const YingYangSiL = JSON.parse(sessionStorage.yingyang);
    console.log(YingYangSiL.result[0].ShengZhuHuoJi)
    export default {
        name:'YingYangSiLiao',
        data(){
            return {
                msg:'集市下面的优质饲料组件',
                myname:{
                    name:'营养饲料',
                    IsSubPage:true
                },
                dataList:[],
                up:'',
                dow:'',
            }
        },
        components:{
            'v-headr':Headr,
        },
        methods:{
            goToSerch(kind){
                window.location.href="/JiShi/ZongHeChaXun?hotCity="+kind;
            },
            chilClick(){
                this.$emit('childByValue',this.IsSubPage)
            },
            /*获取数据*/
            getListData(){
                this.axios.get('/flxoa/CropOffer',{
                    params:{
                        CropCategoryID:'0552',
                    }
                }).then(res => {
                    console.log("营养饲料列表部分成功"+res)
                    this.dataList = res.data[0].data
                    //console.log(res.data[0].data[0])
                    //console.log(JSON.stringify(res))
                    console.log(this.dataList)
                    
                },err => {
                     console.log("营养饲料列表部分失败"+err)
                })
                .catch(err=> {
                    console.log("捕获的错误")
                })


            },
        },
        mounted(){
            this.chilClick()
            this.getListData()
            //this.dataList = YingYangSiL.result[0].ShengZhuHuoJi
        }
    }
</script>
<style type="text/css" scoped>
    .jiageD{
        color: #279637;
        font-size: 16px;
       /* display: none;*/
    }
    .jiantou{
        font-size: 18px;
    }
</style>


