<!-- 我的我的预定组件 -->
<template>
    <div id="WoDeDingYue">
        <v-headr :myname="myname"></v-headr>
        <div class="my-subscription-container">
        <ul class="my-subscription-warp" v-for="(item,key) in DingYueData">
            <li class="my-subscription-list bod-list clearfix">
                <div class="subscription-pic float-lft">
                    <img class=" width100 " src="../../assets/img/dingyue-pic1.png"/>
                </div>                
                <div class="bod-list-info float-lft">
                    <div class="subscription-wrap">
                        <span class="subscription-city displ-inl-blk"></span>
                        <p class="subscription-name displ-inl-blk">{{item.Title}}</p>
                        <p class="subscription-num"></p>
                    </div>
                   
                </div>
                <div class="my-subscriptioned float-rigt">
                     <p class="float-rigt subscriptioned text-cent">✓&nbsp;已订阅</p>
                </div>
            </li>
            
        </ul>
    </div>


    <!-- <div class="add-more-con">
        <a class="add-more" href="javascript:;"  @click="addMor()">
            <span class="add-pic"></span>
            <span class="add-btu">添加更多</span> 
        </a>
    </div> -->
    </div>
</template>

<script type="text/javascript">
    import Headr from '../../components/header.vue'
    const DingYue = JSON.parse(sessionStorage.dingyue);
    //console.log(DingYun.result[0].WoDe)

    export default {
        name:'WoDeDingYue',
        data(){
            return {
                msg:'我的订阅组件',
                myname:{
                    name:'我的订阅',
                    IsSubPage:true
                },
                DingYueData:null
               
            }
        },
        /*props:{
            fromjishi
        },*/
        methods:{
            getData(){
                this.DingYueData = DingYue.result[0].WoDe; 
            },
            addMor(){
                this.$router.push({ name: 'GeRenDingYue'})
            },
            init_DY(){
                var scThis = this; 
                var user_id = sessionStorage.userId; 
                this.axios.get('/api/flxoa//Subscribe?tokenid=000000&UserID=admin').then(function(response){ 
                    console.log('--------------------收藏---------------------')
                    console.dir(response.data[0].data);
                    scThis.DingYueData = response.data[0].data;
                });  
            }
            
            

          

        },
        components:{
            'v-headr':Headr,
        },
        mounted(){
            this.init_DY() 
        }
    }
</script>


