<!-- 集市下面的企业直供组件 -->
<template>
	<div id="QiYeZhiGong">
		<v-headr :myname="myname"></v-headr>
		<section class="aui-scrollView QiYeZhiGong"> 
                <ul class="aui-View-head">
                    <li class="active">全国<span class="triangle"></span></li>
                    <li>企业类型<span class="triangle"></span></li>
                </ul>
                <div class="aui-card-list item1">
                    <a href="javascript:void(0);" class="aui-panel-cell">
                        <div class="aui-panel-cell-bd">
                            <h4>天津小麦粉加工企业报价</h4>
                        </div> 
                    </a>
                    <a href="javascript:void(0);" class="aui-panel-cell">
                        <div class="aui-panel-cell-bd">
                            <h4>辽宁小麦粉加工企业报价</h4>
                        </div> 
                    </a>

                    <a href="javascript:void(0);" class="aui-panel-cell">
                        <div class="aui-panel-cell-bd">
                            <h4>辽宁小麦粉加工企业报价</h4>
                        </div> 
                    </a>
                    
                </div>
            </section>
	</div>
</template>

<script type="text/javascript">
	import Headr from '../../components/header.vue'
	export default {
		name:'QiYeZhiGong',
		data(){
			return {
				myname:{
					name:'企业直供',
					IsSubPage:true
				},
				msg:'集市下面的企业直供组件',
				
			}
		},
		components:{
			'v-headr':Headr,
		},
		methods: {
	      childClick () {
	        this.$emit('childByValue', this.IsSubPage)
	      }
	    },
	    mounted(){
	    	this.childClick()
	    }
	}
</script>


