<!-- 集市下面的三大主粮组件 -->
<template>
	<div id="SanDaZhuLiangXQ">
        <!-- <v-headr >{{myname}}</v-headr> -->
		<!-- <section class="aui-scrollView SanDaZhuLiangCont">  
                <div class="aui-card-list"> 

                    <a  href="javascript:void(0);"
                       class="aui-panel-cell"
                       v-for="key in datas"> 
                        <div class="aui-panel-cell-bd">
                            <h4>{{key.title}}</h4>
                            <span>{{key.date}}</span>
                        </div> 
                        <div class="float-rigt">
                            <em class="jiage">{{key.price}}元</em>
                        </div>
                    </a>

                </div>
            </section> -->

        <header class="head-container title-container header_title" >
            <div class="aux_back"  @click="backPrv()"></div>
            <p class="message-title text-cent">{{myname}}</p>    
        </header>
        

             <div class="price-dispcard-warp" style="margin-top: 44px;">
                <ul class="price-dispcard clearfix">
                    <li class="price-dispcard-item bord-b today-price float-lft text-cent">
                        <p class="price-dispcard-num">{{jinrijiage}}</p>
                        <p class="price-dispcard-text">今日价格</p>
                    </li>
                    <li class="price-dispcard-item bord-b bord-lft yesterday-price float-rigt text-cent">
                        <p class="price-dispcard-num">{{zuorijiage}}</p>
                        <p class="price-dispcard-text">昨日价格</p>
                    </li>
                    <li class="price-dispcard-item bord-b lastWwk-price float-lft text-cent">
                        <p class="price-dispcard-num">{{shangzhoujunj}}</p>
                        <p class="price-dispcard-text">上周均价</p>
                    </li>
                    <li class="price-dispcard-item bord-b bord-lft lastWwk-rise float-rigt text-cent">
                        <p class="price-dispcard-num lastWwk-rise-up">
                            <span class="up"></span><em class="price-num">{{shangzhouzhangfy}}</em><span>%</span>
                           <!--  <span class="dow col-green">-</span><em class="price-num col-green">1.87</em><span class="col-green">%</span> -->
                        </p>
                        <p class="price-dispcard-text">上周涨幅</p>
                    </li>
                </ul>
            </div>

        <div class="bazaar-title">
            <div class="act-bazaar-title-det quotation">{{myname}}</div>
        </div>

        <div class="trend-chart-con">
            <ul class="trend-num-wrap clearfix">
                <li class="trend-num-item float-lft text-cent">
                    <p class="price-dispcard-num">{{benzhoujunjia}}</p>
                    <p class="price-dispcard-text">本周籼稻平均价格(吨/元)</p>
                </li>
                <li class="trend-num-item trend-num-bar float-lft"></li>
                <li class="trend-num-item float-rigt text-cent">
                     <p class="price-dispcard-num lastWwk-rise-up">
                        <span class="up"></span><em class="price-num">{{benzhouzhangfu}}</em><span>%</span>
                    </p>
                    <p class="price-dispcard-text">本周籼稻涨势(吨/元)</p>
                </li>
            </ul>
            <div class="trend-chart">
                <div id="chartDaoMi"  style="height: 200px; padding-bottom: 20px" >
                         图表
                 </div>
            </div>
        </div>

         



	</div>
</template>
<style scoped>
    #Headr{ height: 44px; }
</style>
<script type="text/javascript">

  // const SanDa_ZhuLiang =  sessionStorage.SanDa;
  // const dataList = JSON.parse(SanDa_ZhuLiang);
  // console.dir(dataList)
    //import Headr from '../../components/header.vue'

    let echarts = require('echarts/lib/echarts')
    require('echarts/lib/chart/line')
    // 引入提示框和title组件
    require('echarts/lib/component/tooltip')
    require('echarts/lib/component/title')
	export default {
		name:'SanDaZhuLiang_XQ',
		data(){
			return { 
                isA:0,
				msg:'集市下面的三大主粮组件',
                /*myname:{
                    name:'三大主粮',
                    IsSubPage:true
                },*/
                myname:'',
                jinrijiage:2320,
                zuorijiage:2320,
                shangzhoujunj:2320,
                shangzhouzhangfy:0,
                benzhoujunjia:2320,
                benzhouzhangfu:0,
                data1:['2320', '2320', '2320', '2320', ''],
                data2:['2310', '2310', '2310', '2310', ''],
                data3:['2720', '2720', '2720', '2720', ''],
                datas:null,
                /*稻米*/
                optionDaoMi:{
                tooltip: {
                    trigger: 'axis'
                },
                legend: {
                    x : 'center',
                    y : 'bottom',
                   /* data:['早籼稻','中晚籼稻','粳稻']*/
                },          
                xAxis: {
                    type: 'category',
                    axisLine: {
                        lineStyle: {
                            color: '#e4e4e4'
                        }
                    },
                    axisLabel:{
                        textStyle: {
                            color: '#7b7d7f'
                        }
                    },
                    type: 'category',
                    data:  ['10/22', '10/23', '10/24', '10/25', '10/26']
                },
                yAxis: {
                    type: 'value',
                    color:'#7b7d7f',
                    axisLine:{       //y轴
                      show:false,
                    },
                    axisTick:{       //y轴刻度线
                      show:false
                    },
                    axisLabel:{
                        textStyle: {
                            color: '#7b7d7f'
                        }
                    },
                    splitLine: {
                       lineStyle: {
                            color: '#e4e4e4'
                        }
                    },
                    min:0,
                    
                },
                grid:{
                    x:50,
                    y:20,
                    x2:5,
                    y2:50
                },
                series: [{
                    name:'早籼稻',
                    itemStyle: {
                        normal: {
                            color: "#21be27"
                        }
                    },
                    showSymbol: false,
                    data: [] ,
                    type: 'line',
                    smooth: true
                }]
                },








				
			}
		},
        components:{
           /* 'v-headr':Headr,*/
        },
		methods:{
            backPrv(){
                this.$router.go(-1)
            },
             loadList:function(){
                 //
             },
             getLIst:function(){
                let qStr = this.$route.query.n;
                console.log(qStr)
                if(qStr == 0){
                    this.myname = '湖南长沙早籼稻报价'
                    this.jinrijiage = 2320
                    this.zuorijiage = 2320
                    this.shangzhoujunj = 2320
                    this.shangzhouzhangfy=0
                    this.benzhoujunjia = 2320
                    this.benzhouzhangfu=0
                    this.optionDaoMi.series[0].data = this.data1
                    this.optionDaoMi.yAxis.min = 2320

                    console.log(this.optionDaoMi.yAxis.min)
                    console.log(this.optionDaoMi.series[0].data)
                }else if(qStr == 1){
                    this.myname = '湖南常德早籼稻报价'
                    this.jinrijiage = 2310
                    this.zuorijiage = 2310
                    this.shangzhoujunj = 2310
                    this.shangzhouzhangfy=0
                    this.benzhoujunjia = 2310
                    this.benzhouzhangfu=0
                    this.optionDaoMi.series[0].data = this.data2
                    this.optionDaoMi.yAxis.min = 2310

                }else if(qStr == 2){
                    this.myname = '广东广州早籼稻报价'
                    this.jinrijiage = 2720
                    this.zuorijiage = 2720
                    this.shangzhoujunj = 2720
                    this.shangzhouzhangfy=0
                    this.benzhoujunjia = 2720
                    this.benzhouzhangfu=0
                    this.optionDaoMi.series[0].data = this.data3
                    this.optionDaoMi.yAxis.min = 2720

                }




                //const query_arr = eval('dataList.result[0].'+qStr);
                //console.log(query_arr)
               // this.datas   = query_arr;
             },
			 goInfo:function(){

             },
             goMore:function(n){
                console.log(n);
             },
             tabToggle:function(index){
                 
             },
             draMarketDate(domId,option){
                let myChart = echarts.init(document.getElementById(domId))
              // 绘制图表
              myChart.setOption(option);
            }








		},
		mounted(){  
            this.getLIst();
            this.draMarketDate('chartDaoMi',this.optionDaoMi)
		}
	}
</script>


