<!-- 集市下面的油脂油料组件 -->
<template>
	<div id="YouZhiYouLiaoCont">
        <v-headr :myname="myname"></v-headr>
		 <section class="aui-scrollView YouZhiYouLiao">  
                <div class="aui-card-list"> 
                     <ul class="search_tool2">
                        <li class="active" @click="All()">全国<span class="down-arrow"></span></li><li>种类<span class="down-arrow"></span></li><li>来源<span class="down-arrow"></span></li><li>排序<span class="down-arrow"></span></li>
                        <ul class="tab-arrow-con" style="display: none;">
                          <li class="ative">全国<span class="float-right h"></span></li> 
                        </ul>

                        <ul class="tab-arrow-con" style="display: none;">
                          <li class="ative">普通大豆<span class="float-right h"></span></li>
                          <li>普通大豆<span class="float-right"></span></li>
                          <li>普通大豆<span class="float-right"></span></li>
                          <li>普通大豆<span class="float-right"></span></li>
                          <li>普通大豆<span class="float-right"></span></li>
                          <li>普通大豆<span class="float-right"></span></li>
                        </ul>

                        <ul class="tab-arrow-con" style="display: none;">
                          <li class="ative">来源1<span class="float-right h"></span></li> 
                        </ul>

                        <ul class="tab-arrow-con" style="display: none;">
                          <li class="ative">按时间<span class="float-right h"></span></li> 
                          <li class="ative">按名称<span class="float-right h"></span></li> 
                        </ul>
                    </ul>


                     <a href="javascript:void(0);"
                       class="aui-panel-cell"
                       v-for="(key,index) in list"
                       @click="goInfo()">

                        <div class="aui-panel-cell-bd">
                            <h4>{{key.title}}</h4>
                            <span>{{key.date}}</span>
                        </div> 
                        <div class="float-rigt">
                            <em class="jiage">{{key.price}}元</em>
                        </div>
                    </a>
                </div>
                

            </section>
	</div>
</template>

<script type="text/javascript">
    const youzhiyouliao =  sessionStorage.youzhi;
    const dataList_YoZhi = JSON.parse(youzhiyouliao); 

    import Headr from '../../components/header.vue'
	export default {
		name:'YouZhiYouLiaoCont',
		data(){
			return {

				msg:'集市下面的油脂油料组件',
                myname:{
                    name:'',
                    IsSubPage:true
                },
                list:null
			}
		},
        components:{
            'v-headr':Headr,
        },
		methods:{
			childClick(){
				 
			},
            All(){
                window.location.href = '/page/YouzhiYouLiaoZHonglei.html?url_addres=/JiShi/YouZhiYouLiao&pCity=大豆';
            },
            getUrlParam(key) {
              // 获取参数
                var url = window.location.search;
                // 正则筛选地址栏
                var reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)");
                // 匹配目标参数
                var result = url.substr(1).match(reg);
                //返回参数值
                return result ? decodeURIComponent(result[2]) : null;
            },
            init_list(){ 
                console.log("-----初始化列表-----");
                var pageName = this.getUrlParam("q");
                console.log(pageName); 
                this.myname.name = pageName;
                console.log("this.myname.name:"+this.myname.name)
                var tmpArr = new Array();
                // if("大豆" == pageName ){
                    var arr1 = dataList_YoZhi.result[0].JinKouDaDou;
                    var arr2 = dataList_YoZhi.result[0].YouYongDaDou;
                    var arr3 = dataList_YoZhi.result[0].ShiYongDaDou;
                    for(let i in arr1){tmpArr.push(arr1[i])}
                    for(let j in arr2){tmpArr.push(arr2[j])}
                    for(let k in arr3){tmpArr.push(arr3[k])}  
                    this.list= tmpArr;
                // } 
                
            },
            goMore(){

            },
            goCity(key){
                // this.$router.push({ name: 'SanDaZhuLiang_Cont', query: { q: key }})
            }
		},
		mounted(){
            this.myname.name = ''
		  	this.childClick();
            this.init_list();

		  }
	}
</script>


